import { Component, OnInit } from '@angular/core';
import { LocalityService } from '../shared/locality.service';
import { Router } from '@angular/router';
import { CITY } from '../shared/CITY';

@Component({
  selector: 'app-wantroom',
  templateUrl: './wantroom.component.html',
  styleUrls: ['./wantroom.component.scss']
})
export class WantroomComponent implements OnInit {
  prof: any;
  instaname: any;
  username: string;
  prop = {
    starts: 0,
    ends: 0
  }
  profile = {
    img: '',
    email: '',
    date: '',
    fullname: '',
    username: '',
    properties: '',
    owned: '',
    mobile: '',
    gender: 'male',
    bio: '',
    update: '',
    dob: '',
    insta: '',
    feed: '',
    _id: ''
  }
  search = '';
  city = CITY;
  positions: any;
  pop = false;

  constructor(private loc: LocalityService, private router: Router) { }

  ngOnInit() {
    this.getloc();
    const prof = {
      username: window.localStorage.getItem('username')
    };
    this.username = window.localStorage.getItem('username');
    if (window.localStorage.getItem('update') === '0') {
      this.loc.userprofile(prof).subscribe(data => {
        this.profile = data[0];
        // this.eat('Sucess', 1);
      });
    } else {
      this.loc.getupdateuser(prof).subscribe(data => {
        this.profile = data[0];
        // this.profile.update = window.localStorage.getItem('update');
        // this.instaname = data[0]['insta'];
        // this.prof.fname = this.profile.fullname;
      });
    }

  }
  getloc() {
    this.loc.getPosition().then(data => {
      console.log(data);
      this.positions = data;
      // this.loc.goord(this.positions['lat'], this.positions['lng']).subscribe(datas => {
      //   console.log(datas);
      // });
    });
  }
  setLoc(pos) {
    this.positions = pos;
    this.pop = true;
  }
}
